*{
	overflow: hidden;
 }
 .button {
	min-width: 100px;
	height: 23px;
	border-radius: 3px;
	
	/* background-color: rgb(135, 207, 255); */
	
	
 }
 
 .disabled {
	background-color: rgba(0, 68, 255, 0.514);
	filter: blur(1px);
	border: none;
	color: black;
 }
 
 .secondary {
	color: rgb(0, 68, 255);
	border: 1px solid rgb(0, 68, 255);
	background-color: transparent;
 }
 
 .default {
	border: none;
	color: white;
	background-color: rgb(0, 68, 255);
 }
 
 .red {
	background-color: red;
	border: none;
	color: white;
 }
 
 .default:hover {
	background-color: rgb(27, 87, 255);
 }
 
 .red:hover {
	background-color: rgb(255, 18, 69);
 }
 
 .secondary:hover {
	background-color: rgb(188, 228, 255);
 }
 
 
 .default:active {
	background-color: rgb(17, 0, 255);
 }
 
 .secondary:active {
	background-color: rgb(159, 217, 255);
 }
 
 .red:active {
	background-color: rgb(226, 49, 87);
 }
 
